<template>
    <div id="pokemon">
        <div class="card">
            <div class="card-image">
                <figure>
                    <img :src="currentImg" :alt="pokemon.name">
                </figure>
            </div>
            <div class="card-content">
                <div class="media">
                    <div class="media-left"></div>
                    <div class="media-content">
                        <p class="title is-4">{{num}} - {{name | upper}}</p>
                        <p class="subtitle is-6">{{pokemon.type}}</p>
                    </div>
                </div>

                <div class="content">
                    <button class="button is-info" @click="trocarImg">{{btnText}}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data(){
        return {
            isFront: true,
            currentImg: '',
            btnText: '',
            pokemon: {
                type: '',
                front: '',
                back: '',
            }
        }
    },
    created: function(){
        axios.get(this.url).then(res => {
            this.pokemon.type = res.data.types[0].type.name
            this.pokemon.front = res.data.sprites.front_default
            this.pokemon.back = res.data.sprites.back_default
            this.currentImg = this.pokemon.front
            this.btnText = "Ver de costas"
            console.log(this.pokemon)
        })
    },
    props: {
        num: Number,
        name: String,
        url: String,
        type: String
    },
    filters: {
        upper: function(value){
            let newName = value[0].toUpperCase() + value.slice(1)
            return newName
        }
    },
    methods: {
        trocarImg: function(){
            if(this.isFront){
                this.isFront = false
                this.currentImg = this.pokemon.back
                this.btnText = "Ver de frente"
            } else {
                this.isFront = true
                this.currentImg = this.pokemon.front
                this.btnText = "Ver de Costas"
            }
        }
    }
}
</script>

<style scoped>
    #pokemon {
        margin-top: 15px;
    }
</style>
